body {
  margin: 0 auto;
  overflow: hidden;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

div.graph-container {
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
div.mxRubberband {
    position: absolute;
    overflow: hidden;
    border-style: solid;
    border-width: 1px;
    border-color: #0000FF;
    background: #0077FF;
}
.mxCellEditor {
    background: url(data:image/gif;base64,R0lGODlhMAAwAIAAAP///wAAACH5BAEAAAAALAAAAAAwADAAAAIxhI+py+0Po5y02ouz3rz7D4biSJbmiabqyrbuC8fyTNf2jef6zvf+DwwKh8Si8egpAAA7);
    _background: url(data:image/gif;base64,R0lGODlhMAAwAIAAAP///wAAACH5BAEAAAAALAAAAAAwADAAAAIxhI+py+0Po5y02ouz3rz7D4biSJbmiabqyrbuC8fyTNf2jef6zvf+DwwKh8Si8egpAAA7);
    border-color: transparent;
    border-style: solid;
    display: inline-block;
    position: absolute;
    overflow: visible;
    word-wrap: normal;
    border-width: 0;
    min-width: 1px;
    resize: none;
    padding: 0px;
    margin: 0px;
}
.mxPlainTextEditor * {
    padding: 0px;
    margin: 0px;
}
div.mxWindow {
    box-shadow: 3px 3px 12px #C0C0C0;
    background: url(data:image/gif;base64,R0lGODlhGgAUAIAAAOzs7PDw8CH5BAAAAAAALAAAAAAaABQAAAIijI+py70Ao5y02lud3lzhD4ZUR5aPiKajyZbqq7YyB9dhAQA7);
    _background: url(data:image/gif;base64,R0lGODlhGgAUAIAAAOzs7PDw8CH5BAAAAAAALAAAAAAaABQAAAIijI+py70Ao5y02lud3lzhD4ZUR5aPiKajyZbqq7YyB9dhAQA7);
    border:1px solid #c3c3c3;
    position: absolute;
    overflow: hidden;
    z-index: 1;
}
table.mxWindow {
    border-collapse: collapse;
    table-layout: fixed;
      font-family: Arial;
    font-size: 8pt;
}
td.mxWindowTitle {
    background: url(data:image/gif;base64,)  repeat-x;
    _background: url(data:image/gif;base64,) repeat-x;
    text-overflow: ellipsis;
    white-space: nowrap;
     text-align: center;
     font-weight: bold;
     overflow: hidden;
    height: 13px;
    padding: 2px;
     padding-top: 4px;
     padding-bottom: 6px;
     color: black;
}
td.mxWindowPane {
    vertical-align: top;
    padding: 0px;
}
div.mxWindowPane {
    overflow: hidden;
    position: relative;
}
td.mxWindowPane td {
      font-family: Arial;
    font-size: 8pt;
}
td.mxWindowPane input, td.mxWindowPane select, td.mxWindowPane textarea, td.mxWindowPane radio {
      border-color: #8C8C8C;
      border-style: solid;
      border-width: 1px;
      font-family: Arial;
    font-size: 8pt;
     padding: 1px;
}
td.mxWindowPane button {
    background: url(data:image/gif;base64,R0lGODlhCgATALMAAP7+/t7e3vj4+Ojo6OXl5e/v7/n5+fb29vPz8/39/e3t7fHx8e7u7v///wAAAAAAACH5BAAAAAAALAAAAAAKABMAAAQ2MMlJhb0Y6c2X/2AhjiRjnqiirizqMkEsz0Rt30Ou7y8K/ouDcEg0GI9IgHLJbDif0Kh06owAADs=) repeat-x;
    _background: url(data:image/gif;base64,R0lGODlhCgATALMAAP7+/t7e3vj4+Ojo6OXl5e/v7/n5+fb29vPz8/39/e3t7fHx8e7u7v///wAAAAAAACH5BAAAAAAALAAAAAAKABMAAAQ2MMlJhb0Y6c2X/2AhjiRjnqiirizqMkEsz0Rt30Ou7y8K/ouDcEg0GI9IgHLJbDif0Kh06owAADs=) repeat-x;
      font-family: Arial;
      font-size: 8pt;
      padding: 2px;
    float: left;
}
img.mxToolbarItem {
    margin-right: 6px;
    margin-bottom: 6px;
    border-width: 1px;
}
select.mxToolbarCombo {
    vertical-align: top;
    border-style: inset;
    border-width: 2px;
}
div.mxToolbarComboContainer {
    padding: 2px;
}
img.mxToolbarMode {
    margin: 2px;
    margin-right: 4px;
    margin-bottom: 4px;
    border-width: 0px;
}
img.mxToolbarModeSelected {
    margin: 0px;
    margin-right: 2px;
    margin-bottom: 2px;
    border-width: 2px;
    border-style: inset;
}
div.mxTooltip {
    box-shadow: 3px 3px 12px #C0C0C0;
    background: #FFFFCC;
    border-style: solid;
    border-width: 1px;
    border-color: black;
    font-family: Arial;
    font-size: 8pt;
    position: absolute;
    cursor: default;
    padding: 4px;
    color: black;
}
div.mxPopupMenu {
    box-shadow: 3px 3px 12px #C0C0C0;
    background: url(data:image/gif;base64,R0lGODlhGgAUAIAAAOzs7PDw8CH5BAAAAAAALAAAAAAaABQAAAIijI+py70Ao5y02lud3lzhD4ZUR5aPiKajyZbqq7YyB9dhAQA7);
    _background: url(data:image/gif;base64,R0lGODlhGgAUAIAAAOzs7PDw8CH5BAAAAAAALAAAAAAaABQAAAIijI+py70Ao5y02lud3lzhD4ZUR5aPiKajyZbqq7YyB9dhAQA7);
    position: absolute;
    border-style: solid;
    border-width: 1px;
    border-color: black;
}
table.mxPopupMenu {
    border-collapse: collapse;
    margin-top: 1px;
    margin-bottom: 1px;
}
tr.mxPopupMenuItem {
    color: black;
    cursor: pointer;
}
tr.mxPopupMenuItemHover {
    background-color: #000066;
    color: #FFFFFF;
    cursor: pointer;
}
td.mxPopupMenuItem {
    padding: 2px 30px 2px 10px;
    white-space: nowrap;
    font-family: Arial;
    font-size: 8pt;
}
td.mxPopupMenuIcon {
    background-color: #D0D0D0;
    padding: 2px 4px 2px 4px;
}
.mxDisabled {
    opacity: 0.2 !important;
    cursor:default !important;
}

div.mxTooltip {
    filter:progid:DXImageTransform.Microsoft.DropShadow(OffX=4, OffY=4, 
        Color='#A2A2A2', Positive='true');
}
div.mxPopupMenu {
    filter:progid:DXImageTransform.Microsoft.DropShadow(OffX=4, OffY=4, 
        Color='#C0C0C0', Positive='true');
}
div.mxWindow {
    _filter:progid:DXImageTransform.Microsoft.DropShadow(OffX=4, OffY=4, 
        Color='#C0C0C0', Positive='true');
}
td.mxWindowTitle {
    _height: 23px;
}
.mxDisabled {
    filter:alpha(opacity=20) !important;
}

div.mxPopupMenu {
    background: white;
}

div.mxPopupMenu {
    box-shadow: 3px 3px 12px #c0c0c0;
    position: absolute;
    border-style: solid;
    border-width: 1px;
    border-color: rgba(0, 0, 0, 0.705);
}

table.mxPopupMenu {
    border-collapse: collapse;
    margin-top: 1px;
    margin-bottom: 1px;
}

tr.mxPopupMenuItem {
  transition: all 0.1s ease-in-out;
  color: black;
  cursor: pointer;
}

tr.mxPopupMenuItemHover {
    background-color: rgb(189, 188, 188);
    color: black;
    cursor: pointer;
}

td.mxPopupMenuItem {
    padding: 2px 30px 2px 10px;
    white-space: nowrap;
    font-family: Helvetica,Arial;
    font-size: 8pt;
}

td.mxPopupMenuIcon {
    background-color: #d0d0d0;
    padding: 0px;
}

html td.mxPopupMenuIcon {
    background-color:white;
    padding:0px;
}

div.mxTooltip {
    position: absolute;
    background: rgba(245, 245, 245, 0.692);
    font-size: 8pt;
    color: black;
    padding:6px;
}

.graphted-flex-container{
    display: flex;
    width: 100%;  
}
/* source adapted from: https://codesandbox.io/s/r52m86019m?file=/src/MxGraphGridEditor.js:20369-20378 */

.sidebar {
    font-size: 12px;
    text-align: center;
    overflow: visible;
    margin: 0;
    padding: 0;
    left: 0;
    right: 0;
    bottom: 0;
    list-style: none;
    padding-left: 10px;
    padding-top: 10px;
    background-color: #f8f9fa;
    border-right: 1px solid #dee2e6;
  }
  .sidebar li {
    list-style: none;
    margin: 0;
    padding: 3px 5px;
    margin-bottom: 6px;
    width: 80px;
  }
  .sidebar li.node {
    border: 1px solid #6c757d98;
    cursor: pointer;
  }
