.flipX {
    -webkit-transform: scaleX(-1);
            transform: scaleX(-1);
  }

.flipY {
    -webkit-transform: scaleY(-1);
            transform: scaleY(-1);
}

.navbar{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;
}

.offset-to-navbar {
    margin-top: 4rem;
}



/*always wrap tabs*/
.p-tabview-nav  {
  flex-wrap: wrap; 
  width: 100% !important;
}

.p-tabview-panels {
  padding-left: 0px;
  padding-bottom: 0px;
  padding-right: 0px;
  padding-top: 0px;
}

/* tabview use always full width */
.primeTabViewArea{
  border-left: 9px solid #f4f4f4;
  width: 20%;
  z-index: 3;
 }
.custom-scrollbar .p-scrollpanel-wrapper {
    border-right: 9px solid #f4f4f4;
}

.custom-scrollbar .p-scrollpanel-bar {
    background-color: #1976d2a6;
    opacity: 1;
    transition: background-color .3s;
}

.custom-scrollbar .p-scrollpanel-bar:hover {
    background-color: #135ba1;
}

